import React, { Component } from 'react';
import Header from '../components/Header';
import Section from '../components/Section';
import Footer from '../components/Footer';
import './Home.scss';

class App extends Component {

  state = {
    isLoading : true
  }

  componentDidMount() {
    setTimeout(()=>{
      this.setState({
        isLoading : false
      })
    }, 5000);
  }

  render() {
    return (
      <div className="wrap">
        <Header />
        <Section />
        <Footer />
      </div>
    );
  }
}

export default App;