import React, { Component } from 'react';
import axios from 'axios';
import Movies from './Movies';
import Loading from './Loading';
import './Section.scss';

class Section extends Component {
    state = {
        kim : [],
        isLoading : true
    }

    getMovies =  async () => {
        const { data:{ data:{ movies } } } = await axios.get("https://yts.mx/api/v2/list_movies.json")
        this.setState({
            kim : movies
        })
    }
    
    // render() 수행 후 호출되는 함수 componentDidMount()
    componentDidMount() {
        setTimeout(() => {
            this.setState({
                isLoading : false
            })
        }, 5000);    
        this.getMovies()      
    }

    render() {
        const { kim, isLoading } = this.state;
        return (
            <>
                {
                    isLoading ? <Loading /> : (
                        <section className="container">
                            {
                                kim.map((data)=>(
                                <Movies key={data.id} year={data.year} title={data.title} summary={data.summary} poster={data.medium_cover_image} genres={data.genres} detailImg={data.large_cover_image} />
                                ))   
                            }
                        </section>
                    )
                }
            </>
        );
    }
}

export default Section;